import http from './http'

let key = '89b6d2836434b2e7fb34a08b68019ac4'
const lnglatApi = {
    getLnglat(adresses) {
        let maxRequest = 10//一次只能请求10个地址，高德api限制
        let addressMap = []
        for (let i = 0, len = adresses.length; i < len; i += maxRequest) {
            addressMap.push(adresses.slice(i, i + maxRequest));
        }
        console.log(addressMap)
        let promises = []
        for (let i = 0; i < addressMap.length; i++) {
            //这里的请求，没有加间隔，即只分出了请求，每次十个地址，但是并没有在每个get请求间隔出一定时间再请求下一个
            promises.push(http.get('https://restapi.amap.com/v3/geocode/geo?key=' + key + '&address=' + addressMap[i].join('|') + '&batch=true&output=JSON'))
        }
        return Promise.all(promises)
    }
}
export default lnglatApi
