<template>
  <div id="editor">
    <vue-json-editor v-model="json" :show-btns="false" :expandedOnStart="true"
                     @json-change="onJsonChange"></vue-json-editor>
    <divider/>
    <!--    <Button type="primary" @click="requestData">查询经纬度坐标</Button>-->
    <!--    <Button type="primary" @click="getGeoJson">生成geojson数据</Button>-->
    <Button type="primary" @click="getRandomEvents">近一段时间</Button>
    <div style="padding: 10px"/>
    <Input v-model="results" type="textarea" readonly :autosize="{minRows:10,maxRows:15}"></Input>
  </div>
</template>

<script>
import vueJsonEditor from 'vue-json-editor'
import lnglatApi from '../api/lnglatApi'
// import data from '../cities.json'
import randomFromArray from '../utils/randomData'
import locations from '../locations.json'
//获取地址坐标数据格式样例（可在界面上直接修改）
// let jsonData = ["济南", "青岛", "淄博", "枣庄", "东营", "烟台", "潍坊", "济宁", "泰安", "威海", "日照", "临沂", "德州", "聊城", "滨州", "菏泽"]
let jsonData = {...locations}
export default {
  data() {
    return {
      json: jsonData,
      results: ''
    }
  },

  components: {
    vueJsonEditor
  },

  methods: {
    onJsonChange(value) {
      console.log('value:', value)
    },
    getCoordinates(location) {
      let arr = location.split(',')
      arr[0] = arr[0] * 1
      arr[1] = arr[1] * 1
      return arr
    },
    getRandomEvents() {
      let industries = ["火电", "钢铁", "水泥", "电解铝", "煤炭", "冶金", "化工", "石化", "建材", "造纸", "酿造", "制药", "发酵", "纺织", "制革和采矿业"] //污染行业
      let baseDate = new Date()
      let maxHour = 120 // 向前推5天（120小时）
      let oneHour = 3600 * 1000;
      let locationNames = [...Object.keys(locations)]
      let time2EventId2Event = {}
      for (let time = maxHour; time >= 0; time--) {
        let currentTime = new Date((baseDate.getTime() - time * oneHour)).getTime();
        if (!time2EventId2Event[currentTime])
          time2EventId2Event[currentTime] = {}
        let newEventCount = Math.floor(Math.random() * 3) + 1 //这个小时新增事件的数量
        let eventLocations = randomFromArray(locationNames, newEventCount)
        for (let i = 0; i < newEventCount; i++) {
          let steps = Math.floor(Math.random() * 3) + 1 //这个事件，会跨度几个小时
          let eventId = eventLocations[i] + '--' + currentTime
          let industry = industries[Math.floor(Math.random() * industries.length)]
          let event = {
            id: eventId,
            name: eventLocations[i] + '--' + currentTime + '',
            industry,
            location: {
              name: eventLocations[i],
              lnglat: locations[eventLocations[i]]
            },
            timeRange: [currentTime, currentTime + (oneHour * steps)],
            newsList: []
          }
          for (let j = 0; j < steps; j++) {
            let newsCount = Math.floor(Math.random() * 2) + 1 // 这个step中，有几条新闻
            for (let k = 0; k < newsCount; k++) {
              let news = {
                id: eventId + '-' + j + '-' + k,
                title: '媒体' + (j + '-' + k) + '报道：' + eventLocations[i] + '一' + industry + '工厂发生污染事故',
                time: currentTime,//发生时间
                reportTime: currentTime,// 报道时间
                url: 'http://www.baidu.com'
              }
              event.newsList.push({...news})
            }
          }
          //对于多跨度的新闻，每个time对应的eventId2Event，都要放当前event
          for (let j = 0; j < steps; j++) {
            if (!time2EventId2Event[currentTime + (j * oneHour)])
              time2EventId2Event[currentTime + (j * oneHour)] = {}
            time2EventId2Event[currentTime + (j * oneHour)][eventId] = {...event}
          }
        }
      }
      this.results = JSON.stringify(time2EventId2Event, null, 4)
    },
    getGeoJson() {
      let result = {
        "type": "FeatureCollection",
        "features": []
      }
      for (let i = 0; i < this.json.length; i++) {
        result.features.push({
          "type": "Feature",
          "geometry": {
            "type": "Point",
            "coordinates": this.getCoordinates(this.json[i].location),
          },
          "properties": {
            id: i,
            address: this.json[i].formatted_address,
          }
        })
      }
      this.results = JSON.stringify(result, null, 4)
    },
    requestData() {
      let data = [...this.json]
      console.log(data)
      lnglatApi.getLnglat(data).then((responses) => {
        let resultList = []
        for (let i = 0; i < responses.length; i++) {
          resultList = resultList.concat(responses[i].geocodes)
        }
        this.results = JSON.stringify(resultList, null, 4)
      })
    }
  }
}
</script>
<style>
#editor .jsoneditor-vue {
  height: 500px;
}

#editor .json-save-btn {
  margin-top: 10px;
  cursor: pointer;
  width: 100px;
  line-height: 30px;
  font-size: 14px;
}
</style>
